.coverBackground{
    margin-top: 50px;
    min-height: 400px;
    width: 100%;
    margin-bottom: 100px;
    background-color: rgb(104, 104, 104);
    vertical-align: center;
}

.contact{
    min-width: 10rem;
    min-height: 400px;
    background-color: rgb(104, 104, 104);
}

.contactContainer{
    justify-content: center;
    display: grid;
    grid-auto-flow: column;
    margin-bottom: 50px;
}

.contactContainer h3{
    text-align: center;
    font-size: 14px;
}

.personalInfoContainer{
    min-height: 100px;
    display: grid;
    align-content: center;
    margin-bottom: 100px;
}

.formContainer{
    min-height: 200px;
    margin-left: 250px;
}

.input{
    margin-bottom: 25px;
}

.formContainer label{
    text-align: center;
}

.messageInput{
    min-height: 100px;
}

.inputText{
    width: 300px;
}

.submitButton{
    color: white;
    background-color: black;
    cursor: pointer;
}

.submitButton:hover{
    background-color: rgb(32, 32, 32);
}
