.aboutme{
    margin-top: 50px;
    margin-bottom: 100px;
    margin-left: 25px;
    min-height: 800px;
    display: grid;
    grid-auto-flow: column;
    justify-content: center;
    width: 97%;
    background-color: rgb(24, 24, 24);
}

.aboutme img{
    margin-top: 125px;
    margin-left: 50px;
    margin-right: 50px;
}

.bio{
    margin-left: 50px;
    max-width: 700px;
}

.qualifications{
    display: grid;
    grid-auto-flow: row;
    min-width: 100vh;
    min-height: 400px;
    background-color: rgb(104, 104, 104);
    margin-bottom: 100px;
}

.qualification{
    min-width: 10rem;
    display: grid;
    grid-template-columns: 10rem 70rem;
    justify-content: center;
}

.qualification p{
    margin-left: 100px;
    margin-bottom: 100px;
    max-width: 75%;
}

.title{
  display: grid;
  align-content: center;
  margin-bottom: 100px;
}

.titles{
    margin-left: 25px;
    text-align: center;
}

.skills{
    min-height: 200px;
    width: 100%;
    background-color: rgb(104, 104, 104);
    margin-bottom: 50px;
}

.iconRow{
    margin-bottom: 100px;
}

.leftIcon{
    position: relative;
    margin-left: 25%;
    transform: translateX(-75%);
}

.middleIcon{
    position: absolute;
    left: 50%;
    margin-left: -75px;
}

.rightIcon{
    position: absolute;
    left: 75%;
    margin-left: -75px;
}