.project{
    margin-top: 50px;
    margin-bottom: 50px;
    min-height: 200px;
    width: 100%;
    background-color: rgb(104, 104, 104);
}

.projectDescription{
    position: relative;
    font-size: 24px;
    margin-left: 50%;
    transform: translateX(-50%);
    max-width: 75%;
}

.projectLink{
    margin-left: 25%;
    padding-left: 5px;
    padding-right: 5px;
    padding-top: 2px;
    padding-bottom: 4px;
    margin-bottom: 50px;
    font-size: 24px;
    border-radius: 10px;
    text-decoration: none;
    color: white;
    background-color: rgb(49, 49, 49);
    position: relative;
    margin-left: 25%;
    transform: translateX(-75%);
}

.projectLink:hover{
    background-color: rgb(182, 182, 182)
}

.empty{
    color:rgb(104, 104, 104)
}

.projectOrientation{
    display: grid;
    justify-content: center;
}

.projectContainer{
    max-width: 1500px;
}